import React, { useRef, useState } from 'react';
import Styles from './SignUp.module.css';
import axios from '../utils/axios';
import { useNavigate } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { ToastContainer, toast,Slide } from 'react-toastify';

const SignUp = () => {

  const nameRef = useRef()
  const emailRef = useRef()
  const phoneRef = useRef()
  const passwordRef = useRef()
  const cityRef = useRef()
  const ageRef = useRef()
  const diseaseRef = useRef()
  const workTypeRef = useRef()
  const mealsNumberRef = useRef()

  const navigate = useNavigate();
  const dispatch=useDispatch();

  const [workType, setWorkType] = useState("physical")
  

  const signUpHandler = async(e)=>{
    e.preventDefault();
    
    const name=nameRef.current.value.trim();
    const email=emailRef.current.value.trim();
    const phone=phoneRef.current.value;
    const password=passwordRef.current.value.trim();
    const city=cityRef.current.value.trim();
    const age=ageRef.current.value;
    const disease=diseaseRef.current.value.trim();
    const mealsNumber=mealsNumberRef.current.value;

    if (!email) return toast.warning('Please Enter Email');
    if (!password) return toast.warning('Please Enter Password');
    if (!name) return toast.warning('Please Enter Name');
    if(!phone) return toast.warning('Please Enter Phone');
    if(!city) return toast.warning('Please Enter City');
    if(!age) return toast.warning('Please Enter Age');
    if(!disease) return toast.warning('Please Enter Disease');
    if(!workType) return toast.warning('Please Enter Work Type');
    if(!mealsNumber) return toast.warning('Please Enter Meals Consumed');

    try {
        const formData = new FormData();
        formData.append('name', name);
        formData.append('email', email);
        formData.append('phone', phone);
        formData.append('password', password);
        formData.append('age', age);
        formData.append('city', city);
        formData.append('disease', disease);
        formData.append('workType', workType);
        formData.append('mealsNumber', mealsNumber);
        
        console.log(formData);

        const { data } = await axios.post('auth/signup', formData, {
            headers: {
                'Content-Type': 'application/json',
            },
        });

        dispatch({type:'SET_USER',payload:data.user});

        navigate('/home');
        
        nameRef.current.value="";
        emailRef.current.value="";
        phoneRef.current.value="";
        passwordRef.current.value="";
        cityRef.current.value="";
        ageRef.current.value="";
        diseaseRef.current.value="";
        mealsNumberRef.current.value="";

        
    } catch (error) {
        toast.error(error.response?.data?.message || 'Error signing up');
    }

  }

  return (
    <div className={Styles['signUpPage']}>

        <div className={Styles['imgSection']}>
          <img width={'100%'} height={'100%'} src='https://cdn-icons-png.flaticon.com/512/10423/10423409.png'></img>
        </div>

        <div className={Styles['formSection']}>

          <ToastContainer position="top-center" transition={Slide} autoClose={60000} /> 
          
          <h1 className={Styles['pageHead']}>SignUp !!!</h1>

          <form onSubmit={signUpHandler}>

            <h3 className={Styles['formInputLabel']}>Name</h3>
            <input className={Styles['formInput']} ref={nameRef} placeholder='Enter Your Name'/>
            
            <h3 className={Styles['formInputLabel']}>Email</h3>
            <input className={Styles['formInput']} ref={emailRef} placeholder='Enter Your Email'/>
            
            <h3 className={Styles['formInputLabel']}>Phone</h3>
            <input type='Number' className={Styles['formInput']} ref={phoneRef} placeholder='Enter Your Phone Number'/>
            
            <h3 className={Styles['formInputLabel']}>City</h3>
            <input className={Styles['formInput']} ref={cityRef} placeholder='Enter Your City'/>
            
            <h3 className={Styles['formInputLabel']}>Password</h3>
            <input className={Styles['formInput']} ref={passwordRef} placeholder='Enter Your Password' type='password'/>

            <hr></hr>

            <h3 className={Styles['formInputLabel']}>Age</h3>
            <input type='Number' min={'0'} max={'100'} className={Styles['formInput']} ref={ageRef} placeholder='Enter Your Age'/>

            <h3 className={Styles['formInputLabel']}>Any Major Disease</h3>
            <input className={Styles['formInput']} ref={diseaseRef} placeholder='Enter any major disease if you have'/>

            <h3 className={Styles['formInputLabel']}>WorkType</h3>
            <input type="radio" name="workType" value="physical"  onChange={() => setWorkType("physical")}/> Physical
            <input type="radio" name="workType" value="onscreen"  onChange={() => setWorkType("onscreen")}/> On-Screen

            <h3 className={Styles['formInputLabel']}>Number of Meals Per Day</h3>
            <input className={Styles['formInput']} ref={mealsNumberRef} placeholder='Enter Number of meals you consume per day '/>
            
            <div className={Styles['signUpBtnDiv']}>
                <button className={Styles['signUpBtn']}>SignUp</button>
            </div>
            
            

          </form>  
            
        </div>
    </div>
  )
}

export default SignUp;