import React, { useState } from 'react'
import Style from "./BreatheExercise.module.css"

const BreatheExercise = () => {

    const [message, setMessage] = useState("Breathing Exercise");

    const displayMsg=(msg)=>{
        setMessage(msg);
    }

    const exerciseHandler = () => {
        setTimeout(() => displayMsg("Breathe In"), 0);
        setTimeout(() => displayMsg("Hold On"), 5000);
        setTimeout(() => displayMsg("Breathe Out"), 10000);
        setTimeout(() => displayMsg("Hurrayyy U Did It !!"), 15000);
    }


  return (
    <div className={Style['exerciseContainer']}>
        <div className={Style['msgDiv']}>{message}</div>
        <div className={Style['btnDiv']}>
            <button onClick={exerciseHandler} className={Style['exerciseBtn']}>Start</button>
        </div>
    </div>
  )
}

export default BreatheExercise