import React from 'react'
import { Route, Routes } from 'react-router-dom'
import { useSelector } from 'react-redux';
import SignUp from './Pages/SignUp'
import Login from './Pages/Login'
import Welcome from './Pages/Welcome'
import Home from './Pages/Home';
import ChangeLocation from './Pages/ChangeLocation';

const App = () => {

  const userData=useSelector(state=>state.userReducer);
  

  return (
    <div className='App'>

      <Routes>
        {userData.isLoggedIn ? <Route path='/home' element={<Home/>}/> : <Route path='/home' element={<Login/>}/>}
        {userData.isLoggedIn ? <Route path='/updateLocation' element={<ChangeLocation/>}/> : <Route path='/updateLocation' element={<Login/>}/>}
        <Route path='/' element={<Welcome/>}/>
        <Route path='/login' element={<Login/>}/>
        <Route path='/signup' element={<SignUp/>}/>
      </Routes>
      
    </div>
  )
}

export default App