import React, { useEffect, useState } from 'react'
import Style from "./home.module.css"
import axios from "../utils/axios"
import WeatherSection from './WeatherSection';
import Form from './Form';
import { useSelector } from 'react-redux';
import Checklist from './Checklist';
import Response from './Response';
import { ToastContainer, toast,Slide } from 'react-toastify';
import BreatheExercise from './BreatheExercise';

const Home = () => {

  const [fetched, setfetched] = useState(false);
  const [weather, setweather] = useState()

  const userData=useSelector(store=>store.userReducer);

  const [dataExist, setDataExist] = useState(false);
  

  useEffect(() => {
    
    async function getWeather() {
      
      const {data} = await axios.post('user/weather');
      setweather(data.data);
      setfetched(true);
      return
    }

    const checkData=()=>{
      
      if(userData.healthStatus.length>0){
        let userDate=userData.healthStatus[0].date.substring(0,10);
        let today=new Date().toISOString().substring(0,10);
        if(userDate===today){
          setDataExist(true);
        }
      }
      return;
    }
    getWeather();
    checkData();

  }, [])
  
  return (
    <div className={Style['homePage']}>
      
      <div className={Style['pageLeftSection']}>

        <ToastContainer position="top-center" transition={Slide} autoClose={60000} /> 

        {dataExist && <Checklist data={userData.healthStatus[0].checklist} setDataExist={setDataExist}/>}    

        {!dataExist && <Form email={userData.email} setDataExist={setDataExist} />}

        {dataExist && <Response data={userData.healthStatus[0].response} />}

      </div>

      <div className={Style['pageRightSection']}>
          {fetched && <WeatherSection data={weather} />}

          <BreatheExercise/>
      </div>

    </div>
  )
}

export default Home