import React from 'react'
import Style from "./NavMenu.module.css"
import axios from "../utils/axios"
import { useSelector } from 'react-redux'
import { useNavigate } from 'react-router-dom'
import { toast } from 'react-toastify'

const NavMenu = () => {

    const userData= useSelector(store=>store.userReducer);
    const navigate=useNavigate();

    const logoutHandler=async () => {
        let email=userData.email;
        const{data}=await axios.post('auth/logout',{email});
        if(data.success){
            navigate('/');
            window.location.reload();
        }
    }

    const locationHandler=async () => {
        navigate('/updateLocation');
    }

  return (

    <div className={Style['navMenuPage']}>
        <button className={Style['navMenuBtn']} onClick={locationHandler}>Change Location</button>
        <button className={Style['navMenuBtn']} onClick={logoutHandler}>Logout</button>
    </div>

  )
}

export default NavMenu