import React, { useRef, useState } from 'react'
import { Rating } from 'primereact/rating';
import Style from "./home.module.css";
import axios from "../utils/axios";
import Response from './Response';
import { useDispatch } from 'react-redux';
import { ToastContainer, toast,Slide } from 'react-toastify';


const Form = ({email,setDataExist}) => {

    const [stress, setStress] = useState("no");
    const [bodyPain, setBodyPain] = useState("no");
    const [headache, setHeadache] = useState("no");
    const [food, setFood] = useState("veg");
    const [exercise, setExercise] = useState("no");
    const [alcohol, setAlcohol] = useState("no");
    const [mood, setMood] = useState("happy");
    
    const screenTimeRef = useRef()
    const waterRef = useRef()
    const sleepHoursRef = useRef()

    const dispatch=useDispatch();

    const submitHandler =async()=>{
        const screenTime=screenTimeRef.current.value;
        const water=waterRef.current.value;
        const sleepHours=sleepHoursRef.current.value;
        
        if(!stress){
            toast.warning("Please tell us you are feeling stressed or not");
        }

        if(!water){
            toast.warning("Please enter your Water glasses of today");
        }

        if(!bodyPain){
            toast.warning("Please tell us you are having body-pain or not");
        }

        if(!headache){
            toast.warning("Please tell us you are feeling headache or not");
        }

        if(!screenTime){
            toast.warning("Please tell us about your screen time");
        }

        if(!sleepHours){
            toast.warning("Please tell how many hours you sleep last night");
        }

        if(!food){
            toast.warning("Please tell your food preference");
        }

        if(!exercise){
            toast.warning("Please tell us did you exercised today");
        }

        if(!alcohol){
            toast.warning("Please tell us did you consume alcohol today");
        }

        if(!mood){
            toast.warning("Please tell us about your Mood");
        }

        try {
            const advice = await axios.post("user/advice",{
                stress,water,bodyPain,headache,screenTime,sleepHours,food,exercise,alcohol,mood,email
            });
            let data=advice.data.data;
            let responseData = await axios.post('user/createAlert',{data,email});
            dispatch({type:'SET_USER' , payload:responseData.data.user});
            setDataExist(true);
            
            sleepHoursRef.current.value="";
            screenTimeRef.current.value="";
            waterRef.current.value="";

        } catch (error) {
            toast.error(error.response?.data?.message || 'Error in generating your day routine');    
        }
        
    }

  return (
    <div>

        <h2>Ready for your daily health boost ??</h2>

        <fieldset className={Style['formContainer']}>

            <legend className={Style['formHead']}>Wellness Check</legend>
            
            <div className={Style['formInputDiv']}>
                <label>Are you feeling Stress ??</label>
                <input type="radio" name="stress" value="yes"  onChange={() => setStress("yes")} className={Style['option1']}/> Yes
                <input type="radio" name="stress" value="no"  onChange={() => setStress("no")} className={Style['option2']} /> No
            </div>

            <div className={Style['formInputDiv']}>
                <label>Are you having Body Pain ??</label>
                <input type="radio" name="bodyPain" value="yes"  onChange={() => setBodyPain("yes")} className={Style['option1']} /> Yes
                <input type="radio" name="bodyPain" value="no"  onChange={() => setBodyPain("no")} className={Style['option2']}/> No
            </div>

            <div className={Style['formInputDiv']}>
                <label>Are you having Headache ??</label>
                <input type="radio" name="headache" value="yes"  onChange={() => setHeadache("yes")} className={Style['option1']} /> Yes
                <input type="radio" name="headache" value="no"  onChange={() => setHeadache("no")} className={Style['option2']}/> No
            </div>


            <div className={Style['formInputDiv']}>
                <label>What's your mood today ??</label>
                <input type="radio" name="mood" value="happy"  onChange={() => setMood("happy")} className={Style['option1']} /> Happy
                <input type="radio" name="mood" value="sad"  onChange={() => setMood("sad")} className={Style['option2']}/> Sad
            </div>
            
            <hr></hr>

            <div className={Style['formInputDiv']}>
                <label>Today's Screen Time  </label>
                <input ref={screenTimeRef} type='Number' min={0} max={24} placeholder='Enter screen time for today in hours' className={Style['inpBox']}></input>
            </div>


            <div className={Style['formInputDiv']}>
                <label>Last Night Sleeping Hours</label>
                <input ref={sleepHoursRef} type='Number' min={0} max={24} placeholder='Enter number of hours you sleep last night' className={Style['inpBox']} ></input>
            </div>

            <div className={Style['formInputDiv']}>
                <label>Did you exercise today ??</label>
                <input type="radio" name="exercise" value="yes"  onChange={() => setExercise("yes")} className={Style['option1']} /> Yes
                <input type="radio" name="exercise" value="no"  onChange={() => setExercise("no")} className={Style['option2']} /> No
            </div>

            <hr></hr>

            <div className={Style['formInputDiv']}>
                <label>Water Glasses </label>
                <input ref={waterRef} type='Number' min={0} max={50} placeholder='Enter number of glasses of water' className={Style['inpBox']} ></input>
            </div>

            <div className={Style['formInputDiv']}>
                <label>Your Food Preference for today </label>
                <input type="radio" name="food" value="veg"  onChange={() => setFood("veg")} className={Style['option1']}/> Veg
                <input type="radio" name="food" value="non-veg"  onChange={() => setFood("non-veg")} className={Style['option2']}/> Non-Veg
            </div>
            

            <div className={Style['formInputDiv']}>
                <button onClick={submitHandler} className={Style['submitBtn']}>Advice</button>
            </div>

        </fieldset>

    </div>
  )
}

export default Form