import React from 'react'
import Style from "./ChecklistPage.module.css"
import { useEffect } from 'react'
import { ToastContainer, toast,Slide } from 'react-toastify';
import { useDispatch, useSelector } from 'react-redux';
import axios  from '../utils/axios';


const Checklist = ({data,setDataExist}) => {

  const userData=useSelector(store=>store.userReducer);
  const dispatch=useDispatch();
  
  const checkArr=[];
  const timeSlots=[];

  data.forEach(element => {
    let hour=element.time.split(":")[0];
    let phase=element.time.split(":")[1][3]; 
    if(phase==="A"){
      hour=(hour==="12") ? "00" : hour
    }
    else{
      hour=(hour==="12") ? hour : (parseInt(hour)+12).toString()
    }
    let singleAlert={
      hour:hour,
      minute:element.time.split(":")[1].substring(0,2),
      message:element.msg
    }  
    timeSlots.push(singleAlert);
    checkArr.push(element.done);
  });
  

  function getDelayUntilTime(hour, minute) {
    const now = new Date();
    const target = new Date();
    target.setHours(hour, minute, 0, 0);

    if (target <= now) return null;

    return target - now;
  }


  useEffect(() => {
    const timers = [];
    
    timeSlots.forEach(({ hour, minute, message }) => {
      const delay = getDelayUntilTime(hour, minute);
      if (delay !== null) {
        const timerId = setTimeout(() => {
          
          toast(message);
        }, delay);
        timers.push(timerId);
      }
    });

    return () => timers.forEach(clearTimeout); // Clean up timers on unmount
  }, []);


  const checkHandler=(index)=>{
    checkArr[index]=!checkArr[index]; 
    
  }
  
  const updateHandler=async()=>{

    try {
      let email=userData.email;
      const {data}=await axios.post('user/saveCheckList',{email,checkArr});;
      dispatch({type:'SET_USER',payload:data.user});
      setDataExist(true);
      if(data.success){
        toast.success("CheckList Updated");
      }  
    } catch (error) {
      toast.warning("Error in Updating Checklist");  
    }
    
  }
  

  return (
    <div className={Style['checkListPage']}>

        <div className={Style['checkListCircle']}>&#9678;</div>
        <div className={Style['checkListBox']}>
          <div className={Style['checkListHead']}>Today's Healthy Rituals</div> 
          {
            data.map((item,index)=>(
              <div key={index}  className={Style['listItem']} >

                {
                  (checkArr[index]) ? (<div>✅ {item.time}: {item.msg}</div>) : (<div> <input className={Style['checkListInp']} type='checkbox' val={item._id} onChange={()=>checkHandler(index)} />{item.time}: {item.msg} </div>)  
                }
                   
              </div>
            )) 
          }
          <button className={Style['updateBtn']} onClick={updateHandler}>Update</button>
        </div>
        
    </div>
  )
}

export default Checklist