import {useSelector} from 'react-redux';
import React, { useState } from 'react'
import {NavLink, useNavigate} from 'react-router-dom';
import Styles from './NavBar.module.css'
import NavMenu from './NavMenu';


const NavBar = () => {
    const userData = useSelector(state=>state.userReducer);

    const [display, setDisplay] = useState(false);

     const [showMenu, setShowMenu] = useState(false);

    const navigate=useNavigate();

    const headClickHandler=()=>{

      (userData.isLoggedIn) ? navigate('/home') : navigate('/');

    }
    
  return (
    <div className={Styles['navbar']} >

        <button onClick={headClickHandler} className={Styles['navHead']}>HealthMate</button>

          {!userData.isLoggedIn && (
            <button className={Styles['hamburger']} onClick={() => setShowMenu(!showMenu)}>
              &#9776;
            </button>
          )}

         <div className={`${Styles['navLinks']} ${showMenu && !userData.isLoggedIn ? Styles['show'] : ''}`}>

          {userData.isLoggedIn ? (
            <NavLink className={Styles['navItems']} to={'/home'} />) : (<NavLink className={Styles['navItems']} to={'/'} >
              Welcome
            </NavLink>
          )}

          {!userData.isLoggedIn && (
            <NavLink className={Styles['navItems']} to={'/signup'} >
              SignUp
            </NavLink>
          )}

          {!userData.isLoggedIn && (
            <NavLink className={Styles['navItems']} to={'/login'} >
              Login
            </NavLink>
          )}
        </div>
          
        <div>
              {userData.isLoggedIn && 
                <button className={Styles['profileName']} onClick={()=> setDisplay(display ? false : true)}>
                  <img width={'20em'} height={'20em'} src='profile.png'></img>
                  {userData.name}
                </button> 
            }

            {display && <NavMenu/>}
        </div>


    </div>
  )
}

export default NavBar
