import React from 'react'
import Style from './home.module.css'
import { useSelector } from 'react-redux';


const WeatherSection = ({data}) => {
  
  const userData=useSelector(store=>store.userReducer);
  const location=userData.location;

  return (
    <div className={Style['weatherSection']}>
        
        <div className={Style['largeLabel']}>{data.description}</div>
        <div>
            <img width={'80%'} height={'80%'} src='https://static.vecteezy.com/system/resources/thumbnails/010/989/526/small_2x/rainy-weather-3d-rendering-isolated-on-transparent-background-ui-ux-icon-design-web-and-app-trend-png.png' alt='Img' />
        </div>
        
        <div className={Style['largeLabel']}>{data.temperature} &deg;C</div>

        <div className={Style['weatherBottomSection']}>
            <div>{data.humidity} %</div>
            <div>|</div>
            <div>{data.wind} m/sec </div>
        </div>

        <div className={Style['locationSection']}>
          <img width={'10%'} height={'10%'} src="https://png.pngtree.com/png-vector/20230413/ourmid/pngtree-3d-location-icon-clipart-in-transparent-background-vector-png-image_6704161.png"/>
          <span> {location.city}</span>
          <span>, </span>
          <span>{location.state}</span>
        </div>
                
    </div>
  )
}

export default WeatherSection