import axios from '../utils/axios';
import React, { useRef } from 'react'
import { useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import Styles from './Login.module.css';
import { ToastContainer, toast,Slide } from 'react-toastify';



const Login = () => {

  const emailRef= useRef();
  const passwordRef=useRef();
  const dispatch=useDispatch();
  const navigate=useNavigate();

  const loginHandler=async()=>{

    const email=emailRef.current.value.trim();
    const password=passwordRef.current.value.trim();
    
    if (!email) return toast.warning('Please Enter Email');
    if (!password) return toast.warning('Please Enter Password');

    try {

      const formData = new FormData();
      formData.append('email', email);
      formData.append('password', password);
      const {data} = await axios.post('auth/login',formData,{
        headers: {
                'Content-Type': 'application/json',
        },
      });
      dispatch({type:'SET_USER',payload:data.user});
      
      navigate('/home');  

    } catch (error) {
       toast.error(error.response?.data?.message || 'Error while Logging');
    }
    

  }

  return (
      <div className={Styles['loginPage']}>
          <div className={Styles['loginIcon']}>
              <img width={'100%'} height={'100%'} src='https://cdn-icons-png.flaticon.com/512/295/295128.png'/>
          </div>

          <div className={Styles['loginDetails']}>

              <ToastContainer position="top-center" transition={Slide} autoClose={60000} /> 
              
              <h1 className={Styles['pageHead']}>Login !!!</h1>
              
              <h3 className={Styles['formInputLabel']}>Email</h3>
              <input className={Styles['loginInput']} ref={emailRef} type='text' placeholder='Enter Your Email'/> <br/>
              
               <h3 className={Styles['formInputLabel']}>Password</h3>
              <input className={Styles['loginInput']} ref={passwordRef} type='password' placeholder='Enter Your Password'/> <br/>
              <button onClick={loginHandler} className={Styles['loginBtn']}>Login</button>
          </div>
      </div>
  )
}

export default Login