import React, { useRef } from 'react'
import axios from "../utils/axios"
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import Style from "./ChangeLocation.module.css";
import { ToastContainer, toast,Slide } from 'react-toastify';

const ChangeLocation = () => {
    
    const cityRef = useRef();
    const userData=useSelector(store=>store.userReducer);
    const location=userData.location;
    const navigate=useNavigate();
    const dispatch=useDispatch();

    const updateHandler=async()=>{
        let email=userData.email;
        let city=cityRef.current.value.trim();
    
        if(!city){
            toast.warning("Please Enter your New City");
        }

        const{data}=await axios.post('user/updateLocation',{email,city});
        
        if(data.success){
            dispatch({type:'SET_USER', payload:data.user})
            toast.success("Location updated Successfully")
            navigate('/home');
        }
    }


    return (
        <div className={Style['locationPage']}>

            <div className={Style['locationIcon']}>
                <img width={'100%'} height={'100%'} src='https://png.pngtree.com/png-vector/20220916/ourmid/pngtree-red-location-pin-icon-with-colored-folded-map-png-image_6177040.png'/>
                <div>{location.city}, {location.state}</div>
            </div>
            
            <div className={Style['rightSide']}>

                <fieldset className={Style['locationForm']}>

                    <legend className={Style['locationhead']}>Update Location</legend>
                    <label className={Style['locationPageLabel']}>New City</label>
                    <input className={Style['locationPageInp']} ref={cityRef} placeholder='Enter New City'></input>
                    <button className={Style['locationPageBtn']} onClick={updateHandler}>Update</button>

                </fieldset>
                
                <div className={Style['backBtnDiv']}>
                    <button className={Style['backBtn']} onClick={()=>navigate('/home')}>Go Back</button>
                </div>
                
            </div>
            
            
        </div> 
    )
}

export default ChangeLocation