const initialState= {
    name:'',
    email:'',
    phone:'',
    location:{},
    generalHealth:{},
    healthStatus:[],
    isLoggedIn:false
}

function userReducer(state=initialState, action){
    
    switch(action.type){
        
        case 'SET_USER':
            return{
                ...state,
                name:action.payload.name,
                email:action.payload.email,
                phone:action.payload.phone,
                location:action.payload.location,
                generalHealth:action.payload.generalHealth,
                healthStatus:action.payload.healthStatus,
                isLoggedIn:true
            }
        case 'GET_USER':
            return state
        default:
            return state
    }
    
}

export default userReducer;