import React from 'react'
import Styles from './Welcome.module.css'

const Welcome = () => {
  return (
    <div className={Styles['welcomePage']}>
        <div className={Styles['welcomePageHead']}>WELCOME HEALTHY MATE </div>
        <p className={Styles['welcomePageTagline']}>Smarter Routines. Healthier You.</p>

        <div className={Styles['introParas']}>

          <div className={Styles['para1']}>
            Your personalized guide to better living. We believe your body holds the answers — and our AI is here to decode them. Using real-time insights and smart technology, HealthMate recommends daily routines that match your unique lifestyle and health needs. Whether you're feeling low energy, anxious, or simply out of sync, HealthMate helps you realign with science-backed habits designed to support your body and mind. Powered by AI and grounded in wellness — your best day starts here.
          </div>

          

          <div className={Styles['para2']}>
            Start your healthy routine with HealthMate today... 
          </div>

        </div>
        
    </div>
  )
}

export default Welcome